"use strict";

import sa from 'sa-sdk-javascript';
import event from 'events';

class Sensor extends event.EventEmitter {
    constructor(sensors = {}, enable = false, projectPrefix = '', serverUrl = '/', project = '', env = 'production') {
        super();
        this.sensors = sensors;
        this.enable = enable;
        this.projectPrefix = projectPrefix;
        this.serverUrl = serverUrl + project;
        this.env = env;
        this.init();
    }

    init() {
        if (!this.enable) return;

        this.sensors.init({
            server_url: this.serverUrl,
            heatmap: {
                //是否开启点击图，默认 default 表示开启，自动采集 $WebClick 事件，可以设置 'not_collect' 表示关闭
                //需要 JSSDK 版本号大于 1.7
                clickmap: 'default',
                //是否开启触达注意力图，默认 default 表示开启，自动采集 $WebStay 事件，可以设置 'not_collect' 表示关闭
                //需要 JSSDK 版本号大于 1.9.1
                scroll_notice_map: 'not_collect'
            }
        });

        this.registerPage({
            env: this.env
        });

        this.listen();
    }

    registerPage(parma = {}) {
        /**
         * @parma {env,roomID,anchorID} 
         */
        this.sensors.registerPage(parma);
    }

    track(event, parma = {}) {
        this.sensors.track(this.projectPrefix + event, parma);
    }

    listen() {
        this.on('login', user_id => {
            this.sensors.login(user_id);
        })
        this.on('autoTrack', () => {
            this.sensors.quick('autoTrack');
        })
        /**
         * @parma {roomID,anchorID} 
         */
        this.on('registerPage', (parma = {}) => this.registerPage(parma));
        /**
         * @parma {attentionEntrance:'teacherDetail/living'} 
         */
        this.on('attention', (parma = {}) => this.track('attention', parma));
        /**
         * @parma {cancelAttentionEntrance:'teacherDetail/living'} 
         */
        this.on('cancelattention', (parma = {}) => this.track('cancelattention', parma));
        /**
         * @parma {emonticonName:'[:code]',numberPeremonticon} 
         */
        this.on('sendEmonticon', (parma = {}) => this.track('sendEmonticon', parma));
        /**
         * @parma {pencolor,longtime} 
         */
        this.on('pencolor', (parma = {}) => this.track('pencolor', parma));
        /**
         * @parma {fontsize,longtime} 
         */
        this.on('fontsize', (parma = {}) => this.track('fontsize', parma));
        /**
         * @parma {pensize,longtime} 
         */
        this.on('pensize', (parma = {}) => this.track('pensize', parma));
        /**
         * @parma {pageup,pagedown,selepagenumber} 
         */
        this.on('pageturning', (parma = {}) => this.track('pageturning', parma));

        this.on('clean', () => this.track('clean'));
        /**
         * @parma {longtime} 
         */
        this.on('interact', (parma = {}) => this.track('interact', parma));

        this.on('fresh', () => this.track('fresh'));
        /**
         * @parma {settingmicrophone,settingcamerca,issettingsuccess} 
         */
        this.on('setting', (parma = {}) => this.track('setting', parma));

        this.on('sendmessage', (parma = {}) => this.track('sendmessage', parma));
    }

}

export default {
    install(Vue,{enable}) {
        Vue.prototype.$sensor = new Sensor(
            sa,
            enable,
            'cmny_ulive_',
            'https://tr-sa.uuabc.com/sa?project=',
            process.env.VUE_APP_SENSORS_PROJECT,
            process.env.NODE_ENV
        );
    }
}