<template>
  <div
    v-show="backgroundVisible"
    class="container background"
  >
    <transition
      enter-active-class="animated bounceInDown"
      leave-active-class="animated bounceOutDown"
      @before-enter="beforeEnter"
      @after-leave="afterLeave"
    >
      <div
        class="chat-box"
        v-show="visible"
      >
        <div @click="close()">
          <IconFont
            class="close"
            icon="uu-close1"
          />
        </div>
        <div class="header">
          {{$t('support_title',{name:otherName})}}
        </div>
        <ul
          class="list"
          ref="chatList"
        >
          <li
            v-for="(item,index) in list"
            :key="index"
            :class="item.self?'out':'in'"
          >
            <span class="content">
              {{item.message}}
              <template v-if="!item.self">
                <br>#{{item.name||item.user_nickname}}
              </template>
            </span>
          </li>
        </ul>
        <div class="footer">
          <input
            type="text"
            :placeholder="$t('support_placeholder')"
            v-model.trim="message"
            @keyup.enter="sendMessage"
          >
          <button @click="sendMessage">{{$t('support_send')}}</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import IconFont from "@/components/IconFont.vue";

export default {
  name: "GroupChat",
  components: {
    IconFont
  },
  data() {
    return {
      backgroundVisible: false,
      visible: false,
      list: [],
      message: '',
      name: 'I',
      otherName: 'UU',
    }
  },
  methods: {
    init(list, name, otherName) {
      this.list = list;
      this.name = name;
      this.otherName = otherName;
    },
    show() {
      this.visible = true;
      this.scrollToBottom();
      this.$groupChat.shown = true;
    },
    scrollToBottom() {
      this.$nextTick(() => {
        //必须使用nextTick（在下次 DOM 更新循环结束之后执行延迟回调）
        setTimeout(() => {
          const chatListDom = this.$refs.chatList;
          if (chatListDom) chatListDom.scrollTop = chatListDom.scrollHeight;
        })
      });
    },
    sendMessage() {
      if (!this.message) {
        this.alert.show({
          title: this.$t('support_no_message'),
          timer: 2000
        })
        return;
      }
      this.list.push({ message: this.message, name: this.name, self: true })
      this.scrollToBottom()
      this.$groupChat.send(this.message);
      this.message = '';
    },
    recive(message, name) {
      this.list.push({ message, name, self: false })
      this.scrollToBottom()
    },
    close() {
      this.visible = false;
      this.$groupChat.shown = false;
    },
    beforeEnter() {
      this.backgroundVisible = true;
    },
    afterLeave() {
      this.backgroundVisible = false;
    },
    listen() {
      this.$groupChat.on('init', this.init);
      this.$groupChat.on('show', this.show);
      this.$groupChat.on('recive', this.recive);
    }
  },
  created() {
    this.listen();
  }
}
</script>

