<template>
  <ul v-show="visible">
    <li class="side">
      <IconFont
        class="icon-font"
        icon="uu-turn-left"
      />
    </li>
    <li
      class="item"
      v-for="(item,index) in animations"
      :key="index"
      @click="encourage(index,2000)"
    >
      <img
        :src="item.src"
        :alt="item.name"
      >
      <p>{{item.name}}</p>
    </li>
    <li class="side">
      <IconFont
        class="icon-font"
        icon="uu-turn-right" />
    </li>
  </ul>
</template>

<script>
import { IconFont } from '@/plugins/IconFont.js'
import goodJob from '@/assets/image/animate_good_job.png'
import bingo from '@/assets/image/animate_bingo.png'
import youCanDoIt from '@/assets/image/animate_you_can_do_it.png'
import congratulation from '@/assets/image/animate_congratulation.png'
import keepItUp from '@/assets/image/animate_keep_it_up.png'
import amazing from '@/assets/image/animate_amazing.png'

export default {
  name: 'Encourage',
  components:{
    IconFont
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      animations: [
        {
          name: 'Good job!',
          src: goodJob,
        },
        {
          name: 'Bingo!',
          src: bingo,
        },
        {
          name: 'You can do it! ',
          src: youCanDoIt,
        },
        {
          name: 'Congratulation!',
          src: congratulation,
        },
        {
          name: 'Keep it up！',
          src: keepItUp,
        },
        {
          name: 'Amazing!',
          src: amazing,
        },
      ]
    }
  },
  methods: {
    encourage(index, delay) {
      this.$emit('encourage', index, delay);
      this.$encourage.beginAnimation(index, delay);
    }
  },
  mounted() {
    document.getElementsByClassName(this.animateContainer)
  }
}
</script>

