<template>
  <div id="app" class="wrapper">
    <Room ref="room" >
      <Header slot="header" />
    </Room>
    <splash class="splash"></splash>
    <alert class="alert"></alert>
  </div>
</template>

<script>

import { mapState, mapActions } from "vuex";
import Header from "./components/Header.vue";
import Alert from "@/components/BaseAlert.vue";
import Splash from "@/components/Splash.vue";
import Room from "./components/Room.vue";
import autoplay from '@/assets/autoplay/autoplay.mp3';

export default {
  name: "app",
  components: {
    Header,
    Room,
    Alert,
    Splash
  },
  data() {
    return { preview: null };
  },
  computed: {
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    ...mapState("config", {
      config: state => state
    }),
  },
  methods: {
    ...mapActions("user", ["addUser"]),
    ...mapActions("paint", ["startDraw"]),
    ...mapActions("config", ["setConfig"]),
    ...mapActions("room", ["setRoom", "browser", "pushStudent", "endTheClass", "roomUpdateUser"]),
    checkPageProfile() {
      this.splash.progress(3, this.$t("splash_autoplay"));
      const that = this;
      var audio = document.createElement('audio');
      audio.src = autoplay;
      var promise = audio.play();
      audio.volume = 0.01;
      if (promise) {
        promise.then(function () {
          that.getClassInfo();
        }).catch(err => {
          console.log("autoplay 策略检查:不可用", err.name, err.message);
          that.alert.show({
            title: this.$t("autoplay_text"),
            label: this.$t("autoplay_label")
          }, cb => {
            that.getClassInfo();
          });
        });
      }
    },
    async getUserInfo() {
      const that = this;
      const { isSuccess, code, message, result: user } = await this.Api.getUserInfo();

      if (isSuccess) {
        user['user_type'] = that.Global.UserType.TEACHER;
        that.addUser(user);
        this.$i18n.locale = this.user.type == this.Global.UserType.TEACHER ? "en_US" : this.$i18n.locale;
        document.title = this.$t("document_title");
        this.splash.progress(4, this.$t("splash_browser"));
        this.browserCheck(function (Browser) {
          console.log("Browser : ", Browser);
          that.splash.progress(4, that.$t("splash_browser_ok"));
          that.checkPageProfile();
        });
      } else if (code == 101001006) {
        this.alert.show({
          icon: "exit",
          text: this.$t("class_is_over"),
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      } else if ([10008].indexOf(code) != -1) {
        window.location.hash = "classover";
        that.alert.show({
          icon: "exit",
          text: this.$t('class_timeout_text'),
          title: this.$t("class_timeout_title"),
          label: this.$t("button_leave_label"),
        }, () => {
          that.endTheClass().then(url => {
            if (url) {
              window.location.href = url || `/message?code=${code}&message=${message}`;
            }
          });
        });
      } else {
        this.alert.show({
          icon: "exit",
          text: message,
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      }
    },
    async getClassInfo() {
      this.splash.progress(2, this.$t("splash_classinfo"));
      this.startDraw();
      const { isSuccess, code, message, result, now } = await this.Api.getClassInfo();
      if (isSuccess) {
        const { room_id, setting, page, course_num, start_time, end_time, courseware,
          teacher_note,
          courseware_name,
          student_note,
          users
        } = result;
        this.course.load_bak_list = result.setting['courseware'].list || [];
        this.course.load_bak_timeout = result.setting['courseware'].timeout || 10;

        this.setConfig(setting);
        this.setRoom({
          group_name: result.group_name,
          room_id,
          course_num,
          start_time,
          end_time,
          teacher_note,
          courseware_name,
          student_note
        });
        this.setClock(parseInt(start_time), parseInt(now));
        this.course.userType = this.Global.UserType.TEACHER;// this.user.type;
        this.course.dataProvider(result["courseware"]);
        this.splash.progress(2, this.$t("splash_classinfo_ok"));
        if (this.user.type == this.Global.UserType.TEACHER && teacher_note) {
          this.notice();
        }
        this.$refs['room'].initRoom();
      } else {
        this.alert.show({
          icon: "exit",
          text: message,
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      }
    },
    setClock(entry_time, now) {
      this.$clock.startTime = entry_time || Date.now();
      this.$clock.serverTime = now || Date.now();
      this.$clock.start();
    },
    browserCheck(cb) {
      const that = this;
      let Browser = { download: 'https://www.uuabc.com/download.html' };
      const ua = navigator.userAgent.toLowerCase();
      let re = /(msie|firefox|chrome|opera|version).*?([\d.]+)/;
      let m = ua.match(re);
      Browser.name = m[1].replace(/version/, "safari");
      Browser.version = m[2];
      Browser.mainVersion = Number(m[2].split('.')[0]);

      if (['chrome'].indexOf(Browser.name) != -1) {
        if (Browser.name == 'chrome' && Browser.mainVersion > 59) {
          Browser.supported = true;
          this.browser(Browser);
          //正常上课
          cb(Browser);
        } else {
          Browser.supported = false;
          this.browser(Browser);
          //不建议使用
          this.alert.show({
            icon: "exit",
            text: this.$t('browser_text', { name: Browser.name, version: Browser.version }),
            label: this.$t("browser_label"),
            cancellabel: this.$t("browser_download"),
          }, cb, () => {
            location.href = Browser.download;
            cb && cb(Browser);
          });
        }
      } else {
        Browser.supported = false;
        this.browser(Browser);
        //不能上课, 去下载或返回上一页
        this.alert.show({
          icon: "exit",
          text: this.$t('browser_text', { name: Browser.name, version: Browser.version }),
          label: this.$t("browser_download"),
          cancellabel: this.$t("button_leave_label"),
        }, () => {
          location.href = Browser.download;
          cb && cb(Browser);
        }, () => {
          that.endTheClass().then(url => {
            window.location.href = url || `/message?code=0&message="Browser does not support"`;
            cb && cb(Browser);
          });
        });
      }
    }
  },
  created() {
    this.getUserInfo();
  },
  mounted() {
    this.splash.progress(3, this.$t("splash_init"));
  }
};
</script>


