const Types = {
    INIT: 'init',
    SETLINECOLOR: 'SETLINECOLOR',
    STARTINTERACT: 'STARTINTERACT',
    STARTANIMATE: 'STARTANIMATE',
    ENDANIMATE: 'ENDANIMATE',
    STARTWRITE: 'STARTWRITE',
    STARTDRAW: 'STARTDRAW'
}

export default {
    namespaced: true,
    state: {
        width: 800,
        height: 600,
        fontSize: 24,
        lineColor: "#ff3b2f",
        lineWidth: 3,
        activeAction: 'draw',
        animateEnable: false
    },
    mutations: {
        [Types.INIT](state, data) {
            for (var key in data) {
                state[key] = data[key];
            }
        },
        [Types.SETLINECOLOR](state, color) {
            state.lineColor = color;
        },
        [Types.STARTANIMATE](state) {
            state.animateEnable = true;
        },
        [Types.ENDANIMATE](state) {
            state.animateEnable = false;
        },
        [Types.STARTINTERACT](state) {
            state.activeAction = 'interact';
        },
        [Types.STARTWRITE](state, fontSize) {
            if (fontSize) {
                state.fontSize = fontSize;
            }
            state.activeAction = 'write';
        },
        [Types.STARTDRAW](state, lineWidth) {
            if (lineWidth) {
                state.lineWidth = lineWidth;
            }
            state.activeAction = 'draw';
        },
    },
    actions: {
        init({ commit }, data) {
            commit(Types.INIT, data);
        },
        setLineColor({ commit }, color) {
            commit(Types.SETLINECOLOR, color)
        },
        startAnimate({ commit }) {
            commit(Types.STARTANIMATE)
        },
        endAnimate({ commit }) {
            commit(Types.ENDANIMATE)
        },
        startInteract({ commit }) {
            commit(Types.STARTINTERACT)
        },
        startWrite({ commit }, fontSize) {
            commit(Types.STARTWRITE, fontSize)
        },
        startDraw({ commit }, lineWidth) {
            commit(Types.STARTDRAW, lineWidth)
        },
    },
}