<template>
  <div class="header-container">
    <div class="logo" @click="localeChange"></div>
    <ul class="tool-bar">
      <li class="course-num-item">
        <div>{{$t("room_course_num")}}：{{room.courseNum}}</div>
      </li>
      <li class="clock-item">
        {{$t("room_course_duration",{courseDuration})}}
        <Clock class="clock" />
      </li>

      <li class="browser" v-if="!room.supported">
        <img :src="browserAlert" @click="browserAlertHandler" />
      </li>

      <li class="item" @click="notice()">
        <icon-font class="icon-font" icon="uu-notice"></icon-font>
      </li>

      <li class="item" v-if="user.type != Global.UserType.ADMIN" @click="itHelpHandler">
        <icon-font class="icon-font" icon="uu-mic"></icon-font>
      </li>

      <li class="item" @click="refreshClick">
        <icon-font class="icon-font" icon="uu-reflesh"></icon-font>
      </li>

      <li class="item" @click="exitClick()">
        <icon-font class="icon-font" icon="uu-exit"></icon-font>
      </li>
    </ul>
  </div>
</template>
<script>
import Record from "@/service/Record";
import browserAlert from "@/assets/image/browser_alert.png";
import { mapState, mapGetters, mapActions } from "vuex";

import Easemob from './../plugins/easemob';

export default {
  name: "Header",
  components: {
    // ClassTimer
  },
  data() {
    return {
      num: 0,
      browserAlert
    };
  },
  computed: {
    ...mapState("room", {
      room: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("config", {
      config: state => state
    }),
    chinese() {
      return this.$i18n.locale == "zh_CN";
    },
    courseDuration() {
      const { startTime, endTime } = this.room;
      const duration = parseInt((endTime - startTime) / 60);
      return duration;
    }
  },
  methods: {
    ...mapActions("user", ["addUser"]),
    ...mapActions("room", ["setRoom", "endTheClass"]),
    localeChange() {
      this.$i18n.locale = this.$i18n.locale == "zh_CN" ? "en_US" : "zh_CN";
    },
    refreshClick() {
      this.alert.show({
        icon: "refresh",
        title: this.$t("alert_title_refresh_classroom"),
        text: this.$t("alert_text_refresh_classroom"),
        label: this.$t("button_label"),
        cancellabel: this.$t("button_cancel_label")
      }, () => {
        setTimeout(() => {
          window.location.reload();
        })
      });
    },
    async exitClick() {
      let that = this;
      if (this.$clock.startTimeDifference < 0) {
        this.alert.show({
          icon: "exit",
          title: 'LEAVE CLASSROOM',
          text: 'Please remember to come back when the class begins.',
          label: 'exit',
          cancellabel: 'cancel'
        }, async () => {
          Record.save(Record.EXIT, "退出");
          this.endTheClass().then(url => {
            window.location.href = url;
          });
        });
      } else {
        this.alert.show({
          icon: "exit",
          title: this.$t("alert_title_leave_classroom"),
          text: this.$t("alert_text_leave_classroom"),
          label: this.$t("button_leave_label"),
          cancellabel: this.$t("button_cancel_label")
        }, async () => {
          Record.save(Record.EXIT, "退出");
          this.endTheClass(true).then(url => {
            if (url) {
              window.location.href = url;
            }
          });
        });
      }
    },
    notice() {
      const options = { studentNote: this.room.studentNote, teacherNote: this.room.teacherNote, coursewareName: this.room.coursewareName, teacherName: this.user.name };
      this.alert.showHtml({
        html: this.$t('classnote', options),
        timer: 5000
      });
    },
    itHelpHandler() {
      if (easemobim.bind) {
        easemobim.bind({ configId: this.chinese ? this.config.im.sid : this.config.im.tid });
      } else {
        console.warn('Waiting for easemob.js loading to completed!');
      }
    },
    browserAlertHandler() {
      //不建议使用
      this.alert.show({
        icon: "exit",
        text: this.$t('browser_text', { name: this.room.browser.name, version: this.room.browser.version }),
        label: this.$t("browser_label"),
        cancellabel: this.$t("browser_download"),
      }, null, () => {
        location.href = this.room.browser.download;
      });
    }
  },
  created() {
    let easemob = new Easemob(true);
  },
  mounted() {
    window.easemobim = window.easemobim || {};
    easemobim.config = {
      hide: true,
      autoConnect: true,
      visitor: {
        trueName: this.user.name,
        userNickname: this.user.name
      }
    }
  }
};
</script>

