class Easemob {
    constructor(isTeacher) {
        const src = isTeacher ? '//kefu.easemob.com/webim/en-US/easemob.js' : '//kefu.easemob.com/webim/easemob.js';
        let item = document.createElement('script');
        item.setAttribute('src', src);
        item.defer = true

        setTimeout(function () {
            document.querySelector('body').appendChild(item);
        }, 1000);
    }
}

export default Easemob;