const Types = {
    SETROOM: 'setRoom',
    LEAVE: 'LEAVE',
    ENDTHECLASS: 'ENDTHECLASS'
}
import Api from "@/service";

export default {
    namespaced: true,
    state: {
        roomId: 1,
        courseNum: 1,
        startTime: 1,
        endTime: 1,
        isClassOver: false,
        hasLeave: false,
        teacherNote: '',
        coursewareName: '',
        studentNote: '',
        group_name: null,
        supported: true,
        browser: {},
    },
    mutations: {
        ["browser"](state, data) {
            state.supported = data.supported;
            state.browser = data;
        },
        [Types.SETROOM](state, {
            group_name,
            room_id,
            course_num,
            start_time,
            end_time,
            teacher_note,
            courseware_name,
            student_note
        }) {
            state.group_name = group_name;
            state.roomId = room_id;
            state.courseNum = course_num;
            state.startTime = start_time;
            state.endTime = end_time;
            state.teacherNote = teacher_note;
            state.coursewareName = courseware_name;
            state.studentNote = student_note;
        },
        [Types.LEAVE](state) {
            state.hasLeave = true;
        },
        [Types.ENDTHECLASS](state) {
            state.isClassOver = true;
        },
    },
    actions: {
        browser({ commit }, data) {
            commit('browser', data);
        },
        setRoom({
            commit
        }, data) {
            commit(Types.SETROOM, data);
        },
        endTheClass({ commit }, isEndTheClass) {
            return new Promise(async resolve => {
                const {
                    isSuccess,
                    result
                } = await new Api().leaveClass(isEndTheClass ? 0 : 1);
                if (isSuccess) {
                    const {
                        redirect_url,
                        classover
                    } = result;
                    if (!redirect_url) {
                        commit(Types.LEAVE);
                    }
                    if (classover) {
                        commit(Types.ENDTHECLASS);
                    }
                    resolve(redirect_url);
                }
            })
        }
    },
    getters: {

    }
}