import Api from './index'

class Record {
    constructor() {

    }

    ENTER = 1; //进入
    CLEAN = 2; //清屏
    PAGE_CHANGE = 3; //翻页
    MIC = 4; //麦克风状态
    DRAWING = 5; //画图开关状态
    ANIMATE = 6; // 动画开关状态
    DIAMOND = 7; //奖励钻石
    EXCIT = 8; //激励动画
    CHAT = 9; //聊天
    EXIT = 12; //退出
    VIDEO_PUB = 13; //视频开始
    VIDEO_STOP = 14; //视频结束
    PLATFORM = 15; //视频位置状态
    // COURSE_CHANGE = 16; //切换课件
    POINT = 17; //划线
    DRAWING_TEXT = 18; //课件打字
    ANIMATE_INFO = 19; //交互动画
    CLASSOVER = 20; //下课
    MOVEHOUSE = 25; //切换教室
    APPRAISE = 30; //老师给学生评价点

    /**
     * 保存录制信息
     * @param {类型} type 
     * @param {消息内容} msg|JSON 
     * @param {接受者} receive 
     */
    save(type, msg, receive) {
        receive = receive || { id: '', type: '' };
        var data = {
            receiver_id: receive.id,
            receiver_type: receive.type,
            type_id: type,
            content: msg
        };
        return new Api().record(data);
    }
}
export default new Record();