<template>
  <div
    class="background"
    v-show="visible"
  >
    <img
      :src="src"
      alt="classroom animate"
    >
    <audio
      :src="soundSrc"
      ref="audio"
    ></audio>
  </div>
</template>

<script>
import goodJobAnimation from '@/assets/image/animate_good_job.gif'
import goodJobSound from '@/assets/media/animate_good_job.mp3'
import bingoAnimation from '@/assets/image/animate_bingo.gif'
import bingoSound from '@/assets/media/animate_bingo.mp3'
import youCanDoItAnimation from '@/assets/image/animate_you_can_do_it.gif'
import youCanDoItSound from '@/assets/media/animate_you_can_do_it.mp3'
import congratulationAnimation from '@/assets/image/animate_congratulation.gif'
import congratulationSound from '@/assets/media/animate_congratulation.mp3'
import keepItUpAnimation from '@/assets/image/animate_keep_it_up.gif'
import keepItUpSound from '@/assets/media/animate_keep_it_up.mp3'
import amazingAnimation from '@/assets/image/animate_amazing.gif'
import amazingSound from '@/assets/media/animate_amazing.mp3'
import { setTimeout } from 'timers';

export default {
  name: 'EncourageAnimation',
  data() {
    return {
      src: '',
      soundSrc: '',
      list: [
        {
          src: goodJobAnimation,
          soundSrc: goodJobSound,
        },
        {
          src: bingoAnimation,
          soundSrc: bingoSound
        },
        {
          src: youCanDoItAnimation,
          soundSrc: youCanDoItSound
        },
        {
          src: congratulationAnimation,
          soundSrc: congratulationSound
        },
        {
          src: keepItUpAnimation,
          soundSrc: keepItUpSound
        },
        {
          src: amazingAnimation,
          soundSrc: amazingSound
        },
      ],
      visible: false
    }
  },
  methods: {
    beginAnimation(index, delay) {
      this.src = this.list[index].src;
      this.audio.src = this.list[index].soundSrc;
      this.audio.play();
      this.visible = true;
      setTimeout(() => {
        this.visible = false;
        this.src = null;
        this.audio.src = null;
      }, delay)
    },
    listen() {
      this.$encourage.on('beginAnimation', this.beginAnimation);
    }
  },
  mounted() {
    this.listen();
    // this.src = goodJob
    this.audio = this.$refs['audio'];
  }
}
</script>

<style lang="less" scoped>
.background {
  background: rgba(0, 0, 0, 0.3);
}
</style>


