import * as Course from '@/plugins/course-egret.1.3.1.js'
import AlertEvent from '@/utils/AlertEvent'
import SplashEvent from '@/utils/SplashEvent'
import {
    Encourage,
    EncourageAnimation,
    EncourageEvent,
    Painter,
    PainterEvent,
    TextBox,
    TextBoxEvent,
    Clock,
    ClockEvent,
    IconFont,
} from '@/plugins'

const UserType = {
    STUDENT: 1,
    TEACHER: 2,
    PARENT: 3,
    ADMIN: 4
};


export default {
    install(Vue) {
        Vue.prototype.Global = {
            UserType
        };

        Vue.prototype.course = new window.Course();
        Vue.prototype.alert = new AlertEvent();     
        Vue.prototype.splash = new SplashEvent();


        Vue.component(Encourage.name, Encourage);
        Vue.component(EncourageAnimation.name, EncourageAnimation);
        Vue.prototype.$encourage = new EncourageEvent();
        Vue.component(Painter.name, Painter);
        Vue.prototype.$painter = new PainterEvent();
        Vue.component(TextBox.name, TextBox);
        Vue.prototype.$textbox = new TextBoxEvent();
        Vue.component(Clock.name, Clock);
        Vue.prototype.$clock = new ClockEvent();
        Vue.component(IconFont.name, IconFont);
    }
}