
<template>
  <div class="footer">
    <template>
      <ul class="tool-bar">
        <li
          class="item item-aniamte"
          :class="{active:paint.animateEnable}"
          @click="toggleEncourage();"
        >
          <IconFont class="icon-font" icon="uu-animation1" />
        </li>
        <li
          class="item"
          :class="{active:paint.activeAction == 'interact'}"
          @click="startInteract();"
        >
          <IconFont class="icon-font" icon="uu-hand" />
        </li>
        <li
          class="item item-t-fonts"
          :class="{active:paint.activeAction == 'write'}"
          @click="write()"
        >
          <IconFont class="icon-font" icon="uu-text" />
          <span class="sub">{{activeFontSize}}</span>
          <ul class="t-fonts">
            <li
              v-for="(item,index) in fontSizes"
              @click="write(item.value);"
              :class="{active:paint.fontSize===item.value}"
              :key="index"
            >{{item.name}}</li>
            <hr />
          </ul>
        </li>
        <li
          class="item item-line-width"
          :class="{active:paint.activeAction == 'draw'}"
          @click="draw()"
        >
          <IconFont class="icon-font" icon="uu-pen" />
          <span class="sub">{{activeLineWidth}}</span>
          <ul class="line-widths">
            <li
              v-for="(item,index) in lineWidths"
              @click="draw(item.value)"
              :class="{active:paint.lineWidth===item.value}"
              :key="index"
            >{{item.name}}</li>
            <hr />
          </ul>
        </li>
        <li class="item item-color">
          <ul>
            <li v-for="(item,index) in lineColors" @click="setColor(item.value)" :key="index">
              <IconFont class="icon-font" :icon="'uu-color-'+item.name" />
              <IconFont
                v-show="paint.lineColor==item.value"
                class="icon-font icon-font-select"
                icon="uu-color-select"
              />
            </li>
          </ul>
        </li>
        <li class="item" @click="clear()">
          <IconFont class="icon-font" icon="uu-ease" />
        </li>
      </ul>

      <slot></slot>
      <slot name="pagePicker"></slot>
    </template>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    const now = new Date().getTime();
    return {
      UserType: this.Global.UserType,
      lastSetInteractTime: now,
      lastSetFontSizeTime: now,
      lastSetLineWidthTime: now,
      lastSetLineColorTime: now,
      fontSizes: [
        { name: "L", value: 32 },
        { name: "M", value: 24 },
        { name: "S", value: 18 }
      ],
      lineWidths: [
        { name: "3", value: 9 },
        { name: "2", value: 6 },
        { name: "1", value: 3 }
      ],
      lineColors: [
        { name: "red", value: "#ff3b2f" },
        { name: "yellow", value: "#f7d158" },
        { name: "blue", value: "#1b8bfe" },
        { name: "green", value: "#2dc21b" },
        { name: "orange", value: "#ff8000" },
        { name: "purpe", value: "#7c76f7" }
      ]
    };
  },
  computed: {
    ...mapState("paint", {
      paint: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    activeFontSize() {
      let name = '';
      for (let item of this.fontSizes) {
        if (item.value === this.paint.fontSize) {
          name = item.name;
        }
      }
      return name;
    },
    activeLineWidth() {
      let name = '';
      for (let item of this.lineWidths) {
        if (item.value === this.paint.lineWidth) {
          name = item.name;
        }
      }
      return name;
    }
  },
  methods: {
    ...mapActions("paint", [
      "setLineColor",
      "startAnimate",
      "startInteract",
      "startWrite",
      "startDraw",
      "endAnimate"
    ]),
    add0(num) {
      if (num < 10) {
        num = "0" + num;
      }
      return num;
    },
    toggleEncourage() {
      if (this.paint.animateEnable) {
        this.endAnimate()
      } else {
        this.startAnimate()
      }
    },
    setColor(color) {
      const lastColor = this.paint.lineColor;
      if (lastColor == color) return;
      this.setLineColor(color);
      const
        now = new Date().getTime(),
        lastTime = this.lastSetLineColorTime;
      this.lastSetLineColorTime = now;
    },
    write(fontSize = '') {
      const lastFontSize = this.paint.fontSize;
      this.startWrite(fontSize);
      if (!this.paint.activeAction == 'write' || !fontSize || lastFontSize == fontSize) return;
      const
        now = new Date().getTime(),
        lastTime = this.lastSetFontSizeTime;
      this.lastSetFontSizeTime = now;
    },
    draw(lineWidth) {
      const lastLineWidth = this.paint.lineWidth;
      this.startDraw(lineWidth);
      if (!this.paint.activeAction == 'draw' || !lineWidth || lastLineWidth == lineWidth) return;
      const
        now = new Date().getTime(),
        lastTime = this.lastSetLineWidthTime;
      this.lastSetLineWidthTime = now;
    },
    clear() {
      this.$painter.clear();
      this.$textbox.clear();
    },
  }
};
</script>

