<template>
  <div
    class="textbox-container"
    :style="{ 'pointer-events' : enable ? 'auto' : 'none'}"
    @mousedown="startWriteHandler"
  >
    <div
      v-for="(item,index) in list"
      :key="index"
      class="textarea"
      :style="{...item.styleObj, backgroundColor:isActive(index)?'#3e71e066':'unset'}"
      :contenteditable="isActive(index) ? true : false"
      spellcheck="false"
      v-text="item.isInput ? '' : item.text"
      @input="writingHandler($event)"
    ></div>
  </div>
</template>

<script>
import { setTimeout } from 'timers';
export default {
  name: "TextBox",
  props: {
    enable: {
      type: Boolean,
      required: true
    },
    width: {
      type: Number,
      default: 800
    },
    height: {
      type: Number,
      default: 600
    },
    ratio: {
      type: Number,
      default: 1
    },
    fontSize: {
      type: Number,
      default: 20
    },
    color: {
      type: String,
      default: "red"
    }
  },
  data() {
    return {
      list: [],
      activeIndex: null,
    };
  },
  methods: {
    isActive(index) {
      return this.enable && index == this.activeIndex;
    },
    startWriteHandler($event = {}) {
      const { target, offsetX, offsetY } = $event;
      if (target.className != "textbox-container") return;
      let
        x = offsetX / this.ratio,
        y = offsetY / this.ratio;
      x = Number(x.toFixed(1));
      y = Number(y.toFixed(1));
      this.mouseDown = true;
      this.activeIndex = this.list.length;
      this.beginWrite(x, y, this.width, this.height, true);
      this.$emit('beginWrite', x, y);

      setTimeout(() => {
        document.querySelectorAll('.textarea')[this.activeIndex].focus();
      });
    },
    beginWrite(x, y, width, height, isInput) {
      this.$textbox.pushData(this.list[this.list.length - 1]);
      this.list.push({
        text: "",
        isInput: isInput,
        styleObj: {
          left: x + "px",
          top: y + "px",
          maxWidth: (this.width - x) + "px",
          maxHeight: (this.height - y) + "px",
          color: this.color
        }
      });
    },

    writingHandler(el) {
      const {
        offsetLeft,
        offsetTop,
        clientWidth,
        clientHeight,
        innerText: text
      } = el.target;
      const [x, y, width, height, fontSize, color] = [parseInt(offsetLeft), parseInt(offsetTop), parseInt(clientWidth), parseInt(clientHeight), Number(this.fontSize), this.color];
      this.writing(x, y, width, height, fontSize, color, text, true);
      this.$emit("writing", x, y, width, height, fontSize, color, text);
    },

    writing(x, y, width, height, fontSize, color, text, isInput) {
      this.list.splice(this.list.length - 1, 1, {
        text,
        isInput: isInput,
        styleObj: {
          left: x + "px",
          top: y + "px",
          fontSize: fontSize + "px",
          maxWidth: (this.width - x) + "px",
          maxHeight: (this.height - y) + "px",
          color
        }
      });
    },

    clear() {
      this.list = [];
    },

    listen() {
      if (!this.$textbox) return;
      const that = this;
      this.$textbox.on("beginWrite", (x, y, width, height) => {
        this.beginWrite(x, y, width, height, false);
      });

      this.$textbox.on("writing", (x, y, width, height, fontSize, color, text) => {
        this.writing(x, y, width, height, fontSize, color, text, false);
      });

      this.$textbox.on("snapshotChange", (data, step) => {
        console.log(that.$textbox.history.length, step, data.length, data);
        that.list = [];
        data.forEach(item => {
          item && that.list.push(item);
          that.activeIndex = null;
        })
      });

      this.$textbox.on("clear", this.clear);
    }
  },
  created() {
    this.listen();
  },
};
</script>

<style lang="less" scoped>
.textbox-container {
  position: absolute;
  z-index: 4;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  .textarea {
    position: absolute;
    min-width: 1px;
    min-height: 1px;
    width: 300px;
    font-family: "Verdana";
    overflow: hidden;
    outline: 0;
    word-wrap: break-word;
    -moz-user-modify: read-write-plaintext-only;
    -webkit-user-modify: read-write-plaintext-only;
    cursor: auto;
    &.input {
      background-color: #3e71e066;
    }
    &.output {
      background-color: unset;
    }
  }
}
</style>


