const MutationType = {
    ADD: 'addUser',
    SAVE_DEVICE: 'saveDevice',
    POP_LOGIN: 'popLogin'
};
export default {
    namespaced: true,
    state: {
        id: '',
        uuid: null,
        name: '',
        type: 0,
        photo: '',
        token: 20,
        appToken: "",
        popUp: false,
        cameraId: null,
        microphoneId: null
    },
    mutations: {
        [MutationType.ADD](state, data) {
            state.id = data['user_id'];
            state.name = data['nickname'];
            state.type = data['user_type'];
            state.photo = data.avatar;
            state.uuid = data['external_id'];
            state.appToken = data['token'];
            if (state.id) {
                state.token = parseInt([data['user_type'], data['user_id']].join(''));
            } else {
                //游客学生
                state.token = null;
            }
        },
        [MutationType.POP_LOGIN](state, popUp) {
            state.popUp = !state.id && popUp;
        },
        [MutationType.SAVE_DEVICE](state, device) {
            state.cameraId = device.camera;
            state.microphoneId = device.microphone
        }
    },
    actions: {
        [MutationType.ADD]({ commit }, data) {
            commit(MutationType.ADD, data);
        },
        [MutationType.POP_LOGIN]({ commit }, popUp) {
            commit(MutationType.POP_LOGIN, popUp);
        },
        saveDevice({ commit }, device) {
            commit(MutationType.SAVE_DEVICE, device);
        }
    },
    getters: {
        userType(state) {
            return state.type;
        },
        isLogin(state) {
            return !!state.id;
        }
    }
}