import Encourage from '@/components/Encourage.vue'
import EncourageAnimation from '@/components/EncourageAnimation.vue'
import event from 'events';

class EncourageEvent extends event.EventEmitter {
    constructor() {
        super();
    }

    beginAnimation(index, delay) {
        this.emit('beginAnimation', index, delay);
    }

}

export {
    Encourage,
    EncourageAnimation,
    EncourageEvent
}