import BaseEvent from './BaseEvent'
class AlertEvent extends BaseEvent {
    constructor() {
        super();
    }

    add(options, resolve, reject) {
        options.resolve = resolve;
        options.reject = reject;
        if (options.timer) {
            var that = this;
            const tid = setTimeout(() => {
                that.close(options);
                clearTimeout(tid);
            }, options.timer);
        }

        this.emit('add', options);
    }

    show(options, resolve, reject) {
        options.type = 'alert';
        this.add(options, resolve, reject);
    }

    videoSetter(options, resolve, reject) {
        options.type = 'videosetting';
        this.add(options, resolve, reject);
    }

    showHtml(options) {
        options.type = 'html';
        this.add(options);
    }

    tip(text, delay){
        this.emit('tip', {text: text, delay:delay});
    }

    close(options) {
        this.emit("remove", options);
    }
}

export default AlertEvent;