(function (global, factory) {
    "use strict";
    if (typeof define === "function" && define.amd) {
        define([], factory);
    } else if (typeof module != "undefined" && typeof exports == "object") {
        module.exports = factory();
    } else {
        if (typeof window !== "undefined") {
            global = window;
        } else if (typeof global !== "undefined") {
            global = global;
        } else if (typeof self !== "undefined") {
            global = self;
        } else {
            global = this;
        }
        global.Course = factory();
    }
})(this, (function () {
    "use strict";
    /********************************************************
     *      事件处理器
     *********************************************************/
    function EventEmitter() {
        this.events = {};
    }

    /**
     * 绑定事件函数
     * @param eventName
     * @param callback
     */
    EventEmitter.prototype.on = function (eventName, callback) {
        this.events[eventName] = this.events[eventName] || [];
        this.events[eventName].push(callback);
    };

    /**
     * 解除绑定事件函数
     * @param eventName
     */
    EventEmitter.prototype.off = function (eventName) {
        delete this.events[eventName];
    };

    /**
     * 触发事件函数
     * @param eventName
     * @param _
     */
    EventEmitter.prototype.emit = function (eventName, _) {
        var events = this.events[eventName],
            args = Array.prototype.slice.call(arguments, 1), i, m;
        if (!events) {
            return;
        }
        for (i = 0, m = events.length; i < m; i++) {
            events[i].apply(null, args);
        }
    };
    /********************************************************
     *      课件
     *********************************************************/
    function Course() {
        if (typeof document == 'object') {
            document.domain = 'uuabc.com';
        }
        this.EVENT_OPEN = "open";
        this.EVENT_PROGRESS = "progress";
        this.EVENT_COMPLETE = "complete";
        this.EVENT_ERROR = "error";
        this.EVENT_ACTION = "action";
        this.EVENT_TIPS = "tips";
        this.EVENT_PAGE_CHANGE = "pageChange";
        this.EVENT_PAGE_COMPLETE = "pageComplete";
        this.EVENT_MOUSEDOWN = 'mouseDown';
        this.EVENT_MOUSEMOVE = 'mouseMove';
        this.EVENT_MOUSEUP = 'mouseUp';
        this.EVENT_MOUSECLICK = 'mouseClick';
        this.cursor = { icon: 'auto', offsetX: 0, offsetY: 0 };
        this.completed = false;//加载完成
        this.auth = true;
        this.requestCount = 0;
        this.userType = 0;

        this.url = null;
        this.page = 0;
        this.total = 1;
        //iframe对象
        var frame = null;
        var that = this;
        Object.defineProperties(this, {
            version: {
                get: function () {
                    return 'course-egret.1.3';
                }
            },
            contentWindow: {
                get: function () {
                    return frame.contentWindow || {};
                }
            },
            iframe: {
                set: function (dom) {
                    frame = document.querySelector(dom);//获取iframe
                },
                get: function () {
                    return {
                        contentWindow: that.contentWindow,
                        GlobalData: that.contentWindow['GlobalData'] || {},
                        PageMgr: that.contentWindow['PageMgr'] || {},
                    };
                }
            },
            src: {
                set: function (url) {
                    that.url = url;
                    if (url == null) {
                        that.emit(that.EVENT_ERROR, 'error', "课件地址有误:" + url);
                        console.error("Course # loadCourse, 缺少必须url不能为空", url);
                        return;
                    }

                    var loadEvent = function (e) {
                        //方法注册
                        that.contentWindow.callbackPageLoaded = function (a, b) {
                            that.pageLoaded(a, b);
                        };

                        that.contentWindow.callbackSendMsg = function (msg) {
                            that.tackAction(msg);
                        };

                        that.contentWindow.callbackSendInfo = function (msg) {
                            that.tackInfo(msg);
                        };

                        that.contentWindow.progressNum = function (num) {
                            that.emit(that.EVENT_PROGRESS, num);
                        };

                        //鼠标事件
                        that.contentWindow.mouseClick = function (x, y) {
                            that.emit(that.EVENT_MOUSECLICK, x, y);
                        };

                        that.contentWindow.mouseDown = function (x, y) {
                            that.emit(that.EVENT_MOUSEDOWN, x, y);
                        };

                        that.contentWindow.mouseUp = function (x, y) {
                            that.emit(that.EVENT_MOUSEUP, x, y);
                        };

                        that.contentWindow.mouseMove = function (x, y) {
                            that.emit(that.EVENT_MOUSEMOVE, x, y);
                        };

                        that.contentWindow.callbackResLoaded = function (total) {
                            that.requestCount = 0;
                            that.completed = true;
                            that.loadCompleted(total);
                            if (that.currentPage != that.page) {
                                that.initPage(that.page);
                            }
                        };
                    };

                    if (frame.attachEvent) {
                        frame.attachEvent('onload', loadEvent);
                    } else {
                        frame.onload = loadEvent;
                    }
                    frame.src = url;
                    that.completed = false;
                    that.requestCount++;
                    that.emit(that.EVENT_OPEN, url, that.page, that.pages);
                },
                get: function () {
                    return that.url;
                }
            },
            currentPage: {
                get: function () {
                    return that.iframe.PageMgr['cur_page'];
                }
            },
            totalPage: {
                get: function () {
                    return that.iframe.PageMgr['all_page'];
                }
            },
            hidePage: {
                get: function () {
                    return that.totalPage - that.total;
                }
            }
        });
    }
    Course.prototype = new EventEmitter();
    
    /**
     * 加载地址
     * @param url
     */
    Course.prototype.load = function (url) {
        this.src = url;
    };

    /**
     * 刷新页
     * @param url
     */
    Course.prototype.reload = function () {
        this.load(this.url);
    };

    /**
     * 课件数据源
     * @param url   课件地址
     * @param pages     需要显示的页码数组
     */
    Course.prototype.dataProvider = function (url, pages) {
        this.url = url;
        if (pages instanceof Array || !pages) {
            this.pages = pages || [];
        } else if (!Number(pages)) {
            this.dataProvider(url, Number(pages));
        } else {
            pages = Math.abs(parseInt(pages));
            if (pages) {
                this.pages = [];
                while (pages > 0) {
                    this.pages.unshift(pages);
                    pages--;
                }
            } else {
                this.emit(this.EVENT_ERROR, 'error', "数据源异常");
            }
        }
    };

    /**
     * 加载完成
     */
    Course.prototype.loadCompleted = function (num) {
        var that = this;
        if (this.pages.length == 0 || this.pages.length > num) {
            this.total = num;
        } else {
            this.total = this.pages.length;
        }

        if (!this.page || this.page < 0) {
            this.page = 1;
        } else if (this.page > this.total) {
            this.page = this.total;
        }
        this.iframe.GlobalData.userType = this.userType;
        this.emit(this.EVENT_COMPLETE, that.url);
    };

    /**
     * 页面加载完成
     * @param isSingle          是否是单人互动
     * @param isInteraction     当前学生是否可以互动
     */
    Course.prototype.pageLoaded = function (single, action) {
        var that = this;
        this.single = single;
        this.action = action;
        this.emit(this.EVENT_PAGE_COMPLETE, that.page, that.total, single, action);
    };

    /**
     * 发出动画
     * @param msg
     */
    Course.prototype.tackAction = function (msg) {
        this.mockAction(msg);
        this.emit(this.EVENT_ACTION, msg, this.page);
    };

    /**
     * 发出TIPS消息
     * @param msg
     */
    Course.prototype.tackInfo = function (msg) {
        this.emit(this.EVENT_TIPS, JSON.parse(msg), this.page);
    };

    /**
     * 响应动画
     * @param msg
     */
    Course.prototype.mockAction = function (msg) {
        if (this.completed) {
            this.iframe.PageMgr.receiveMsg(msg);
        }
    };

    /**
     * 互动权限
     * @param enabled
     */
    Course.prototype.hasAuthority = function (enabled) {
        if (this.completed) {
            this.auth = enabled;
            if (typeof this.iframe.PageMgr.cursorIcon == 'function') {
                if (enabled) {
                    var cur = this.cursor || { icon: 'auto', offsetX: 0, offsetY: 0 };
                    this.iframe.PageMgr.cursorIcon(cur.icon, cur.offsetX, cur.offsetY);
                } else {
                    this.iframe.PageMgr.cursorIcon("auto", 0, 0);
                }
            }
            this.iframe.GlobalData.hasAuthority(enabled);
        }
    };

    /**
     * 上一页
     */
    Course.prototype.prevPage = function () {
        this.page -= 1;
        if(this.completed){
            if (this.page > 0) {
                this.emit(this.EVENT_PAGE_CHANGE, this.page, this.total);
                if (this.pages.length == 0) {
                    this.iframe.PageMgr.prevPage();
                } else {
                    this.iframe.PageMgr.turnPage(this.pages[this.page - 1]);
                }
            } else {
                this.page = 1;
                this.emit(this.EVENT_ERROR, 'first', "已经是首页");
            }
        }
    };

    /**
     * 下一页
     */
    Course.prototype.nextPage = function () {
        this.page += 1;
        if(this.completed){
            if (this.page <= this.total) {
                this.emit(this.EVENT_PAGE_CHANGE, this.page, this.total);
                if (this.pages.length == 0) {
                    this.iframe.PageMgr.nextPage();
                } else {
                    this.iframe.PageMgr.turnPage(this.pages[this.page - 1]);
                }
            } else {
                this.page = this.total;
                this.emit(this.EVENT_ERROR, 'last', "已经是最后一页");
            }
        }
    };

    /**
     * 初始化页面
     * @param page
     */
    Course.prototype.initPage = function (page) {
        page = Number(page);
        if(this.completed){
            if (page >= 1) {
                if (this.pages.length == 0) {
                    if (page > this.total) {
                        page = this.total;
                    }
                    this.page = page;
                } else {
                    var tp = this.pages[page - 1];
                    if (tp >= this.totalPage) {
                        tp = this.totalPage;
                    }
                    this.page = page;
                    page = tp;
                }
            } else {
                page = 1;
                this.page = page;
            }
            this.emit(this.EVENT_PAGE_CHANGE, this.page, this.total);
            this.iframe.PageMgr.turnPage(page);
        }
    };

    return Course;
}));