<template>
  <transition-group class="bubble-body" 
    enter-active-class="bounceInUp" 
    leave-active-class="bounceOutDown">
    <span v-for="item in list" class="bubble animated" :key="item.time">{{item.text}}</span>
  </transition-group>
</template>

<script>
export default {
  name: 'SocketStatus',
  props: ["text"],
  data() {
    return {
      list: []
    };
  },
  methods: {
    init() {
      const that = this;
      //连接成功
      this.$socket.on('connected', () => {
        that.remove();
      });

      //重新连接
      this.$socket.on('reconnect', (count, total) => {
        that.remove();
        that.list.push({time:Date.now(), text:that.$t('socket_reconnect') + count});
        that.splash.progress(1, that.$t('splash_socket_reconnect') + count);
      });

      //连接失败
      this.$socket.on('close', () => {
        that.remove();
        that.list.push({time:Date.now(), text:that.$t('socket_connect_faild')});
        that.splash.progress(1, that.$t('socket_connect_faild'));
      });
    },
    remove() {
      if (this.list.length > 0) {
        let count = this.list.length;
        while (count--) {
          this.list.splice(count, 1);
        }
      }
    }
  },
  mounted() {
    this.init();
  }
}
</script>

<style lang="less" scoped>
.bubble-body {
  pointer-events: none;
  z-index: 5;
  position: absolute;
  width: 100%;
  background: yellow;
  color: red;
  text-align: center;
  .bubble {
    margin: 6px 0 4px 0;
    width: 100%;
    display: inline-block;
  }
}
</style>


