          :emitEnable="true"
          :receiveEnable="false"
          :width="paint.width"
          :height="paint.height"
          :ratio="page.ratio"
          :lineWidth="paint.lineWidth"
          :lineColor="paint.lineColor"
          @endPaint="endPaintHandler"
