<script>
import { mapState, mapActions } from "vuex";

import Footer from "./Footer.vue";
import CourseTips from "./CourseTips.vue";
import Courseware from "@/components/Courseware.vue";
import CoursePagePicker from "@/components/CoursePagePicker.vue";
import BubbleWindow from "@/components/BubbleWindow.vue";
import Record from "@/service/Record";

import bg_cn from "@/assets/image/course_bg.png";
import bg_en from "@/assets/image/course_bg_en.png";
export default {
  name: "Room",
  components: {
    Footer,
    CourseTips,
    Courseware,
    CoursePagePicker,
    BubbleWindow
  },
  data() {
    return {
      bg_cn,
      bg_en
    };
  },
  computed: {
    ...mapState("page", {
      page: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("paint", {
      paint: state => state
    }),
    bgMask() {
      return "en_US" == this.$i18n.locale ? bg_en : bg_cn;
    },
    paintEnable() {
      return this.paint.activeAction == 'draw';
    },
    writeEnable() {
      return this.paint.activeAction == 'write';
    },
  },
  methods: {
    ...mapActions("page", ["onResize"]),
    ...mapActions("paint", ["init", "endAnimate"]),
    async initRoom() {
      this.$refs["course"].init();
      this.init({ width: 1024, height: 768 });
      this.course.initPage(this.course.page);
    },
    encourage(index, delay) {
      console.log(index, delay);
      setTimeout(() => {
        this.endAnimate();
      }, delay);
    },
    pageChangeHandler(page, total) {
      Record.save(Record.PAGE_CHANGE, { page: page });
    },
    paintUndoHandler() {
      this.$painter.undo();
      this.$textbox.undo();
    },
    paintRedoHandler() {
      this.$painter.redo();
      this.$textbox.redo();
    },
    endPaintHandler() {
      this.$textbox.pushData(null);
    },
    beginWrite() {
      this.$painter.pushData(null);
    }
  },
  created() {
    const that = this;
    this.course.on(this.course.EVENT_OPEN, function () {
      that.splash.progress(5, that.$t("splash_course_init"));
    });
    this.course.on(this.course.EVENT_COMPLETE, function (url) {
      that.splash.complete(that.$t("splash_course_ok"));
    });
  },
  mounted() {
    this.onResize(document.documentElement);
    window.onresize = () => {
      this.onResize(document.documentElement);
    };
  }
};
</script>
