import Painter from '@/components/Painter.vue'
import event from 'events';

class PainterEvent extends event.EventEmitter {
    history = [];
    step = -1;
    constructor() {
        super();
    }

    pushData(imageData) {
        this.step++;
        this.history.push(imageData);
    }

    undo() {
        const that = this;
        if (this.step > 0) {
            this.step--;
            const imageData = this.history[this.step];
            if(imageData){
                var image = new Image();
                image.src = imageData;
                image.onload = function () {
                    that.emit('snapshotChange', image, that.step, that.history.length);
                }
            }
        }else {
            this.step = -1;
            that.emit('snapshotChange', null, that.step, that.history.length);
        }
    }

    redo() {
        const that = this;
        if (this.step < this.history.length - 1) {
            this.step++;
            const imageData = this.history[this.step];
            if(imageData){
                var image = new Image();
                image.src = imageData;
                image.onload = function () {
                    that.emit('snapshotChange', image, that.step, that.history.length);
                }
            }
        }
    }

    beginPaint(x, y, width, color) {
        this.emit('beginPaint', x, y, width, color);
    }

    painting(x, y) {
        this.emit('painting', x, y)
    }

    clear() {
        // this.history = [];
        // this.step = -1;
        this.emit('clear')
    }

}

export {
    Painter,
    PainterEvent
}