import socketio from "socket.io-client";
import event from 'events';

class Socket extends event.EventEmitter {
    constructor() {
        super();
        this.id = 0;
        this.count = 2;
        this.connected = false;

        this.list = [];
        this.storageKey = '';
        //是否是断线重连
        this.isKoConnected = false;
    }

    connect({ path, url, id, options }) {
        this.id = id;
        this.count = options && options.reconnectionAttempts || this.count;
        this.manager = {
            path,
            secure: true,
            transports: ['websocket'], // ['websocket', 'polling']
            reconnection: true, //启动自动连接
            reconnectionAttempts: this.count, //最大重试连接次数
            reconnectionDelay: 2000, //最初尝试新的重新连接等待时间
            reconnectionDelayMax: 10000, //最大等待重新连接,之前的2倍增长
            timeout: 20000 //
        };
        this.url = url;
        Object.assign(this.manager, options);
        console.log(this.url, 'webclient connect', url, this.manager);
        this.connected = false;
        this.socket = socketio.connect(url, this.manager);
        this.listen(this.socket)
    }

    listen(socket) {
        const that = this;
        socket.on('connected', () => {
            console.log(this.url, "socket io","connected success");
            socket.emit("authenticate", "token");
        })

        //身份验证成功 authenticated
        socket.on('authenticated', () => {
            this.save(this.id);
            this.connected = true;
            this.emit('connected');
        });

        //连接时发生错误,err
        socket.on("connect_error", () => {
            console.error(this.url, "socket io", "connect error");
            this.connected = false;
        });

        //连接时超时
        socket.on("connect_timeout", () => {
            console.log(this.url, "socket io", "connect_timeout");
            this.connected = false;
        });

        //断开连接时触发
        socket.on('disconnect', () => {
            that.count--;
            that.isKoConnected = true;
            console.log(this.url, "socket io", that.url, that.count, 'disconnect');
            that.connected = false;
            that.emit('disconnect', that.connected);
            if (that.count <= 0) {
                that.emit('close');
            } else {
                that.emit('disconnect', that.connected);
            }
        });

        //成功重连后触发,num连接尝试次数
        socket.on('reconnect', (num) => {
            console.log(this.url, "socket io", 'reconnect', num);
        });

        //试图重新连接时触发
        socket.on('reconnect_attempt', () => {
            console.log(this.url, "socket io", 'reconnect attempt');
        });

        //试图重新连接中触发, num连接尝试次数
        socket.on('reconnecting', (num) => {
            that.isKoConnected = true;
            console.log(this.url, "socket io", 'reconnecting', num);
            this.emit('reconnect', num, this.manager.reconnectionAttempts);
        });

        //重联尝试错误,err
        socket.on('reconnect_error', () => {
            console.error(this.url, "socket io", 'reconnect error');
            this.connected = false;
        });

        //重连失败
        socket.on('reconnect_failed', () => {
            console.log(this.url, "socket io", 'reconnect failed');
            this.connected = false;
            this.emit('close');
        });

        //error,err
        socket.on('error', (err) => {
            console.error(this.url, "socket io", 'error');
            this.connected = false;
            this.emit('error', err);
        });

        socket.on('enterSuccess', (list) => {
            list = list || [];
            list.forEach((item) => {
                this.emit('userEnter', item.user_id, item.role, item.info);
            });
            this.emit('enterSuccess');
        });

        socket.on('enterReject', (data) => {
            this.emit('enterReject', data.code, data.message);
        });

        socket.on('offline', (data) => {
            this.emit('offline', data.device, data.time);
        });

        socket.on('userEnter', (data) => {
            console.log(this.url, 'userEnter', data.user_id, data.role, data.info);
            this.emit('userEnter', data.user_id, data.role, data.info);
        });

        socket.on('userQuit', (data) => {
            console.log(this.url, 'userQuit', data.user_id, data.role, data.info);
            this.emit('userQuit', data.user_id, data.role, data.info);
        });

        socket.on('share', (data, user) => {
            if (data.event) {
                this.emit('share', data.event, data.data, user)
            }
        });
    }

    join(data) {
        console.log(this.url, 'socket.io join data', data);
        this.socket.emit('join', data)
    }

    share(data) {
        this.socket.emit('share', data)
    }

    //广播消息通道
    broadcast(eventName, data) {
        if (this.connected) {
            this.socket.emit('share', { event: eventName, data: data });
        }
    }

    close() {
        if (this.socket) {
            this.socket.close();
            console.log(this.url, 'socket.io closed');
        }
    }

    init(id, list) {
        this.cr_id = "cr_" + id;
        this.storageKey = 'uuabc_ws_cr';
        this.list = list || [];
    }

    /**
      * 找到一个有效的socket
      */
    getSocketItem() {
        const data = window.localStorage && JSON.parse(window.localStorage.getItem(this.storageKey)) || null;
        var item = null;

        if (data && data.id === this.cr_id) {
            const id = data.wid;
            if (id) {
                this.list.forEach(opt => {
                    if (opt.id == id) {
                        item = opt;
                    }
                });
            } else {
                item = this.list[0];
            }
        } else {
            item = this.list[0];
        }

        return item;
    }

    /**
     * 找到指定位置的下一个
     */
    nextSocketItem(item) {
        var index = 0;
        this.list.forEach((opt, _index) => {
            if (item.id == opt.id) {
                index = _index + 1;
                index = index >= this.list.length ? - 1 : index;
            }
        });
        return this.list[index] || null;
    }

    save(id) {
        window.localStorage && window.localStorage.setItem(this.storageKey, JSON.stringify({
            id: this.cr_id, wid: id
        }));
    }
}

export {
    Socket
}