import GroupChat from '@/components/GroupChat.vue'
import event from 'events';

class GroupChatEvent extends event.EventEmitter {
    constructor() {
        super();
        this.shown = false;
    }

    init(list,name,otherName){
        this.emit('init',list,name,otherName);
    }

    show() {
        this.emit('show');
    }

    recive(message,name){
        this.emit('recive',message,name);
    }

    send(message){
        this.emit('send',message)
    }

}

export {
    GroupChat,
    GroupChatEvent
}