<template>
  <transition-group
    v-show="bgShow"
    class="alert alert-bg"
    enter-active-class="animated bounceIn"
    leave-active-class="animated bounceOutDown"
    @before-enter="beforeEnter"
    @after-leave="afterLeave"
  >

    <template v-for="(item, index) in list">
      <base-window
        v-if="item.type == 'alert'"
        :ref="index"
        :key="item.key"
        :popdata="item"
        @submit="submitClick(item)"
        @cancel="cancelClick(item)"
      ></base-window>

      <video-setter
        v-if="item.type == 'videosetting'"
        :key="item.key"
        :ref="index"
        :popdata="item"
        @submit="submitClick(item)"
        @cancel="cancelClick(item)"
      ></video-setter>

      <div
        v-if="item.type == 'html'"
        :key="item.key"
        class="alert-html"
      >
        <div @click="cancelClick(item)">
          <IconFont
            class="close"
            icon="uu-close1"
          />
        </div>
        <div v-html="item.html">
        </div>
      </div>
    </template>

  </transition-group>
</template>
<script>
import IconFont from "@/components/IconFont.vue";
import BaseWindow from "@/components/BaseWindow.vue";
import VideoSetter from "@/pages/double-teachers/components/VideoSetter.vue";
export default {
  name: "Alert",
  data() {
    return { list: [], bgShow: false };
  },
  components: {
    BaseWindow,
    VideoSetter,
    IconFont
  },
  methods: {
    init() {
      let that = this;
      this.alert.on("add", data => {
        data.key = Date.now() + "-" + that.list.length;
        that.list.push(data);
      });

      this.alert.on("remove", data => {
        that.list.forEach((item, index) => {
          if (item.key == data.key) {
            that.list.splice(index, 1);
          }
        });
      });
    },
    submitClick(item) {
      if (item.closed) {
        var that = this;
        item.resolve &&
          item.resolve(() => {
            that.alert.emit("remove", item);
          });
      } else {
        item.resolve && item.resolve();
        this.alert.emit("remove", item);
      }
    },
    cancelClick(item) {
      item.reject && item.reject();
      this.alert.emit("remove", item);
    },
    beforeEnter() {
      this.bgShow = true;
    },
    afterLeave() {
      this.bgShow = this.list.length > 0;
    }
  },
  created() {
    this.init();
  }
};
</script>
<style lang="less" scoped>
.alert {
  z-index: 510;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &.alert-bg {
    background: rgba(0, 0, 0, 0.55);
    .alert-html {
      position: absolute;
      padding: 40px 30px 30px;
      overflow: hidden;
      background: #6d93f2;
      border-radius: 20px;
      user-select: none;
      min-width: 300px;
      max-width: 500px;
      color: white;
      .close {
        position: absolute;
        cursor: pointer;
        right: 15px;
        top: 15px;
      }
    }
  }
}
</style>


