import AgoraRTC from 'agora-rtc-sdk'
import BaseEvent from '@/utils/BaseEvent'

console.log('设备检测', AgoraRTC.checkSystemRequirements());
/*******************************************************************
 * 本地视频
 *
 * @constructor 依赖 AgoraRTCSDK-2.4.1.js
 *******************************************************************/
class VideoStream extends BaseEvent {
    cameras = [];//视频输入列表
    microphones = [];//音频输入列表
    audios = [];//音频输出列表
    camera = null;//当前使用的视频输入设备
    microphone = null;//当前使用的音频输入设备
    localStream = null;//本地视频流
    videoProfile = null;//视频规格
    videoElement = null;//本地流video播放容器
    streamID = null;
    constructor() {
        super();
    }

    /**
     * 获取视频采集约束数据
     */
    get options() {
        var data = {
            streamID: parseInt(this.streamID),
            screen: false,
            video: false,
            audio: false,
        };
        if (this.cameraId) {
            data.video = true;
            data.cameraId = this.cameraId;
        } else {
            console.warn('没有视频设备, 无视频信号');
        }
        if (this.microphoneId) {
            data.audio = true;
            data.microphoneId = this.microphoneId;
        } else {
            console.warn('没有麦克风设备, 无音频信号');
        }
        return data;
    }

    init() {
        var that = this;
        return new Promise((resolve, reject) => {
            AgoraRTC.getDevices((devices) => {
                that.audios = [];
                that.cameras = [];
                that.microphones = [];
                if (devices && devices.length > 0) {
                    devices.forEach((device) => {
                        device = JSON.parse(JSON.stringify(device));
                        if (device['kind'] == 'videoinput') {
                            device.label = device.label || 'Camera ' + (that.cameras.length + 1);
                            that.cameras.push(device);
                        } else if (device['kind'] == 'audioinput') {
                            device.label = device.label || 'Microphone ' + (that.microphones.length + 1);
                            that.microphones.push(device);
                        } else if (device['kind'] == 'audiooutput') {
                            device.label = device.label || 'Audio ' + (that.audios.length + 1);
                            that.audios.push(device);
                        }
                        delete device['groupId'];
                        delete device['kind'];
                    });
                    resolve([that.cameras, that.microphones, that.audios]);
                } else {
                    reject("DEVICES_NOT_FOUND");
                }
            });
        });
    };

    set cameraId(deviceId) {
        const that = this;
        const list = that.cameras || [];
        list.forEach(element => {
            if (element.deviceId == deviceId) {
                that.camera = element;
            }
        });
    }

    get cameraId() {
        if (this.camera) {
            return this.camera.deviceId;
        }
        if (this.cameras.length > 0) {
            return this.cameras[0].deviceId;
        }
        return null;
    }

    set microphoneId(deviceId) {
        const that = this;
        const list = that.microphones || [];
        list.forEach(element => {
            if (element.deviceId == deviceId) {
                that.microphone = element;
            }
        });
    }

    get microphoneId() {
        if (this.microphone) {
            return this.microphone.deviceId;
        }
        if (this.microphones.length > 0) {
            return this.microphones[0].deviceId;
        }
        return null;
    }

    /** 显示播放本地视频流 */
    display(videoElement) {
        const video = videoElement || this.videoElement;
        if (this.localStream) {
            this.localStream.isPlaying() && this.localStream.stop();
            video.autoplay = true;
            video.srcObject = this.localStream.stream;
            video.play().catch(err => {
                console.warn('localStream 播放视频流失败', err);
            });

            //TODO getSates计算清晰度
        }
    };

    createStream(options) {
        var that = this;
        options = options || that.options;
        this.videoProfile = this.videoProfile || '240P_3';
        console.log('Create Stream', this.element, this.videoProfile, options);
        return new Promise((resolve, reject) => {
            that.localStream = AgoraRTC.createStream(options);
            that.localStream.setVideoProfile(that.videoProfile);
            that.localStream.on("accessAllowed", () => {
                that.access = true;
                console.log("localStream # accessAllowed");
            });
            that.localStream.on("accessDenied", () => {
                that.access = false;
                console.warn("localStream # accessDenied");
            });
            that.localStream.init(() => {
                console.log("getUserMedia successfully");
                resolve(that.localStream);
            }, (err) => {
                console.log("getUserMedia failed", err);
                reject(err.msg);
            });
        });
    };

    switchDevice(type, deviceId) {
        const that = this;
        return new Promise((resolve, reject) => {
            that.localStream.switchDevice(type, deviceId, resolve, reject);
        });
    }

    /** 关闭本地视频流 */
    close() {
        if (this.localStream != null) {
            this.localStream.close();
        }
    };

}


/*******************************************************************
 * 视频流服务
 *
 * @constructor 依赖 AgoraRTCSDK-2.4.1.js
 *******************************************************************/
class VideoClient extends BaseEvent {
    token = null;
    channel = null;
    streamList = {};
    clinet = null;
    appid = null;
    constructor() {
        super();
        AgoraRTC.Logger.setLogLevel(AgoraRTC.Logger.DEBUG);
        AgoraRTC.Logger.enableLogUpload();

        this.client = AgoraRTC.createClient({
            mode: 'live',
            codec: "vp8"
        });

        this.addListener(this.client);
    }

    /**
     * 监听视频流对象
     * @param {Object} client 
     */
    addListener(client) {
        const that = this;
        //远程音视频流已添加回调事件(stream-added)
        client.on('stream-added', (evt) => {
            const stream = evt.stream;
            const uid = stream.getId();
            console.log("New stream added: ", uid);
            console.log("Subscribe ", stream);
            client.subscribe(stream, function (err) {
                console.log("Subscribe stream failed", uid, err);
            });
        });
        //远程音视频流已订阅回调事件(stream-subscribed)
        client.on('stream-subscribed', (evt) => {
            const stream = evt.stream;
            const uid = stream.getId();
            that.streamList[uid] = stream;
            console.log("Subscribe remote stream successfully: ", stream.getId(), stream.hasVideo(), stream.hasAudio(), stream.getAttributes());
            var data = that.getToken(uid);
            that.emit('stream', data.id, data.type, data.token);
        });
        //远程音视频流已删除回调事件(stream-removed)
        client.on('stream-removed', (evt) => {
            const stream = evt.stream;
            const uid = stream.getId();
            const data = that.getToken(uid);
            that.emit('removed', data.id, data.type, data.token);
            stream.stop();
            delete that.streamList[uid];
            console.log("Remote stream is removed " + stream.getId());
        });

        //对方用户已离开会议室回调事件(peer-leave)
        client.on('peer-leave', (evt) => {
            const stream = evt.stream;
            if (stream) {
                const uid = evt.uid;
                const data = that.getToken(uid);
                that.emit('removed', data.id, data.type, data.token);
                stream.stop();
                delete that.streamList[uid];
                console.log(uid, " leaved from this channel");
            }
        });

        //用户已取消视频通话静音
        client.on('unmute-video', (evt) => {
            const uid = evt.uid;
            const data = that.getToken(uid);
            console.log("unmute video:", uid, data);
        });

        //用户已被踢且被封禁
        client.on('client-banned', (evt) => {
            const uid = evt.uid;
            const attr = evt.attr;
            that.emit('rejected', uid, attr);
            console.warn(" user banned:" + uid + ", banntype:" + attr);
        });

        client.on('active-speaker', (evt) => {
            const uid = evt.uid;
            const data = that.getToken(uid);
            console.log("update active speaker: client", uid, data);
        });

        //token 失效前30s提示
        client.on('onTokenPrivilegeWillExpire', () => {
            that.emit('tokenExpire');
        });

        var channelKey = "";
        client.on('error', function (err) {
            console.log("Got error msg:", err.reason);
            that.emit('error', err.reason);
            if (err.reason === 'DYNAMIC_KEY_TIMEOUT') {
                client.renewChannelKey(channelKey, function () {
                    console.log("Renew channel key successfully");
                }, function (err) {
                    console.warn("Renew channel key failed: ", err);
                });
            }
        });
    }

    /**
     * 解析token，获取解析token结果对象
     * @param {String} token 
     */
    getToken(token) {
        token = token + "";
        var result = {
            token: token
        };
        if (token && token.length >= 2) {
            result = {
                token: token,
                type: Number(token.substr(0, 1)),
                id: Number(token.substr(1, token.length - 1))
            };
        } else {
            console.warn('无效token', token);
        }
        return result;
    };

    //更新App token
    renewToken(appToken) {
        this.client.renewToken(appToken);
    }

    /**
     * 初始化
     */
    init(appid) {
        const that = this;
        this.appid = appid;
        console.log("VIDEO CLIENT::", this.appid);
        return new Promise((resolve, reject) => {
            if (!that.appid) {
                console.warn("AgoraRTC appid must not be empty", that.appid);
                reject("AgoraRTC appid must not be empty" + that.appid);
            } else {
                //初始化
                that.client.init(that.appid, function () {
                    console.log("INIT::", that.appid);
                    // Logger.save(Logger.VIDEO, null, "INIT成功 APPID:" + that.appid + ", Time:" + Date.now());
                    resolve();
                }, function (info) {
                    var err = info;
                    console.warn("INIT ERROR::", err);
                    switch (err) {
                        case 'SERVICE_NOT_AVAILABLE':
                            err = '服务不可用, APPID:' + that.appid;
                            break;
                        case 'CONNECT_GATEWAY_ERROR':
                            err = '无法连接 Web 服务器';
                            break;
                        case 'INVALID_OPERATION':
                            err = '请联系技术支持!';
                            break;
                        case 'INVALID_KEY':
                        case 'INVALID_DYNAMIC_KEY':
                        case 'DYNAMIC_KEY_TIMEOUT':
                            err = '请联系技术支持,续费视频服务!';
                            break;
                    }
                    // Logger.save(Logger.VIDEO, null, "INIT失败 APPID:" + that.appid + ", ERROR:" + err);
                    reject(err, info);
                });
            }
        });
    }

    /**
     * 连接声网服务器
     * @param {String} classid 
     * @param {String} appToken  
     * @param {Int32} token 
     */
    connect(classid, appToken, token) {
        const that = this;
        var channel = "td" + classid; //强制转换为字符串
        token = Number(token) || 0;
        return new Promise(function (resolve, reject) {
            // if (!appToken) {
            //     console.error("AgoraRTC appToken must not be empty", appToken);
            //     reject("AgoraRTC appToken must not be empty" +appToken);
            // } else {
            //加入频道
            that.client.join(appToken, channel, token, function (uid) {
                that.token = uid;
                that.channel = channel;
                var data = that.getToken(uid);
                console.log("JOIN 成功", that.appid, channel, uid, data.id, data.type, data.token);
                resolve(data.token, data.id, data.type);
            }, function (info) {
                var err = info;
                console.warn("JOIN ERROR::", err);
                switch (err) {
                    case 'INVALID_PARAMETER':
                        err = '无效 Token#' + appToken;
                        break;
                    case 101:
                        err = '视频服务ID无效 <br> APPID:' + that.appid;
                        break;
                    case 102:
                        err = '课程ID无效 <br> ID:' + classid;
                        break;
                    case 2002:
                        err = '进入教室失败,刷新重试';
                        break;
                    case 109:
                    case 110:
                        err = '请联系技术支持,续费视频服务!';
                        break;
                }
                reject(err, info);
            });
            // }
        });
    };

    /**
     * 发布视频流
     * @param {*} stream 
     */
    publish(stream) {
        var that = this;
        return new Promise(function (resolve, reject) {
            var data = that.getToken(stream.getId());
            that.client.publish(stream, function (err) {
                console.warn("Publish local stream error: ", err);
                reject(err);
            });

            //本地音视频已上传回调事件(stream-published)
            that.client.on('stream-published', function (evt) {
                console.log("Publish local stream successfully");
                resolve();
            });
        });
    };

    /**
     * 查询指定用户的视频流
     * @param {int32} token 
     */
    getStream(token) {
        var stream = this.streamList[token];
        if (stream) {
            return stream;
        } else {
            console.warn('指定token的媒体流已经丢失', token);
        }
        return null;
    };

    player(token, element) {
        var user = this.getToken(token);
        var stream = this.getStream(token);
        if (stream) {
            stream.isPlaying() && stream.stop();

            //清空之前的videodisplay
            if (document.querySelector('#display' + token)) {
                element.removeChild(document.querySelector('#display' + token));
            }

            //容器videodisplay
            let display = document.createElement('div');
            display.setAttribute('id', "display" + token);
            display.setAttribute('style', 'position:absolute; width:100%;height:100%; background:#000');
            element.appendChild(display);

            //视频dom
            let video = document.createElement('video');
            display.appendChild(video);
            video.setAttribute('id', 'remote' + token);
            video.setAttribute('playsinline', true);
            video.setAttribute('style', 'height: 100%; width: 100%; object-fit: cover;');
            video.autoplay = true;
            video.srcObject = stream.stream;
            video.play().catch(err => {
                console.warn(token, 'stream 播放视频流失败', err);
            });
        } else {
            console.warn('指定token的媒体流已经丢失或未获取...', token, element);
        }
    }

    /**
     * 停止播放
     * @param {*} token 
     * @param {*} element 
     */
    stopPlayer(token) {
        console.log('Current Stream List', token, this.streamList);
        const el = document.querySelector('#display' + token);
        el && el.parentNode.removeChild(el);
    };

    /**
     * 开关视频流
     * @param {int32} token 
     * @param {Boolean} enabled 
     */
    toggleVideo(token, enabled) {
        var stream = this.getStream(token);
        if (enabled) {
            stream.enableVideo();
        } else {
            stream.disableVideo();
            console.log('视频图像已经关闭', JSON.stringify(this.getToken(token)));
        }
    };

    /**
     * 开关音频流
     * @param {int32} token 
     * @param {Boolean} muted 
     */
    toggleAudio(token, muted) {
        var stream = this.getStream(token);
        if (stream) {
            if (muted) {
                stream.disableAudio();
                console.log('视频声音已经关闭', JSON.stringify(this.getToken(token)));
            } else {
                stream.enableAudio();
            }
        }
    };

    /**
     * 停止视频流
     * @param {Object} stream 
     */
    stop(stream) {
        const that = this;
        if (stream) {
            const data = that.getToken(stream.getId());
            this.client.unpublish(stream, function (err) {
                console.warn("Unpublish local stream failed", err);
            });
        }
    };

    /**
     * 离开，退出
     */
    exit() {
        var that = this;
        return new Promise(function (resolve, reject) {
            Object.keys(that.streamList).map(key => {
                that.stopPlayer(key);
            });

            that.streamList = {};
            that.client.leave(function () {
                console.log("Leavel channel successfully");
                resolve();
            }, function (err) {
                console.warn("Leave channel failed");
                reject(err);
            });
        });
    };
}

export { VideoStream, VideoClient }
