
import Vue from 'vue'
import Performance from 'web-report'

try {
  Performance({
    domain: '//tr.uuabc.com/apm/report',
    add: {
      appId: process.env.VUE_APP_APM_ID
    }
  })
} catch (e) {
  console.log(e)
}

Vue.config.errorHandler = function (err, vm, info) {
  let {
    message,
    stack
  } = err;

  // Processing error
  let resourceUrl, col, line;
  let errs = stack.match(/\(.+?\)/)
  if (errs && errs.length) errs = errs[0]
  errs = errs.replace(/\w.+js/g, $1 => {
    resourceUrl = $1;
    return '';
  })
  errs = errs.split(':')
  if (errs && errs.length > 1) line = parseInt(errs[1] || 0);
  col = parseInt(errs[2] || 0)

  // Fixed parameters
  // Call the Performance.addError method
  Performance.addError({
    msg: message,
    col: col,
    line: line,
    resourceUrl: resourceUrl
  })
}