<template>
    <transition-group class="bubble-body"
      enter-active-class="bounceInUp"
      leave-active-class="bounceOutDown">
      <span v-for="item in list" class="bubble animated" :key="item.key">{{item.text}}</span>
    </transition-group>
</template>

<script>
export default {
  name: "BaseWindow",
  props: ["text"],
  data() {
    return {
      list: []
    };
  },
  methods: {
    init() {
      const that = this;
      this.alert.on("tip", data => {
        data.key = Date.now() + "-" + that.list.length;
        that.list.push(data);
        //延时关闭
        data.tid = setTimeout(() => {
          that.remove(data);
        }, data.delay || 10000);
      });
    },
    remove(data) {
      const that = this;
      that.list.forEach((item, index) => {
        if (item.key == data.key) {
          that.list.splice(index, 1);
        }
      });
    }
  },
  created() {
    this.init();
  }
};
</script>

<style lang="less" scoped>
.bubble-body {
  pointer-events: none;
  z-index: 5;
  height: 50px;
  width: 100%;
  font-size: 14px;
  display: flex;
  justify-content: center;
  .bubble {
    position: absolute;
    height: 30px;
    line-height: 30px;
    padding: 2px 10px;
    border-radius: 20px;
    color: #fff;
    background: rgba(0, 0, 0, 0.35);
  }
}
</style>
