<template>
  <!--课件翻页区域-->
  <ul class="dropdown">
    <li v-if="!loading" style="width:100%;text-align:center">loading....</li>

    <li class="left-btn" @click="pagePrev" v-if="loading">
      <icon-font class="arrow" icon="uu-turn-left"></icon-font>
    </li>
    <li class="pages" v-if="loading">
      {{currentPage}} / {{totalPage}}
      <icon-font class="arrow-down" icon="uu-arrow-down"></icon-font>
      <div class="page-list-box">
        <ul>
          <li v-for="n in totalPage" :key="n" @click="pageNum(n)">{{n}}</li>
          <!-- {{$t('which_page',{page:add0(n)})}} -->
        </ul>
        <hr />
      </div>
    </li>
    <li class="right-btn" @click="pageNext" v-if="loading">
      <icon-font class="arrow" icon="uu-turn-right"></icon-font>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'CoursePagePicker',
  data() {
    return {
      loading: false,
      currentPage: 1,
      totalPage: 1,
    };
  },
  methods: {
    pagePrev() {
      this.pageNum(this.course.page - 1);
    },
    pageNext() {
      this.pageNum(this.course.page + 1);
    },
    pageNum(page) {
      page = page < 1 ? 1 : page > this.course.total ? this.course.total : page;
      if (this.course.page != page) {
        this.course.initPage(page);
        this.$emit('change', page, this.course.total);
      }
    },
    courseManager() {
      //课件程序请求开示
      this.course.on(this.course.EVENT_OPEN, (url, page, pages) => {
        this.loading = false;
      });

      //课件程序请求加载成功
      this.course.on(this.course.EVENT_COMPLETE, (url) => {
        this.loading = true;
      });

      //页面开始更换
      this.course.on(this.course.EVENT_PAGE_CHANGE, (page, total) => {
        this.currentPage = page;
        this.totalPage = total;
      });

      this.course.on(this.course.EVENT_PAGE_COMPLETE, (page, total) => {
        this.currentPage = page;
        this.totalPage = total;
      });
    }
  },
  created() {
    this.courseManager();
  }
}
</script>

