<template>
  <div class="text-tips">
    <span class="title">{{$t('teach_tips')}}</span>
    <div class="text scroll" :class="{empty: !text}" v-html="text"></div>
  </div>
</template>
<script>
export default {
  name: "CourseTips",
  data() {
    return {
      text: null
    }
  },
  methods: {
    init() {
      this.course.on(this.course.EVENT_TIPS, (text) => {
        this.text = text.msg;
      });
    }
  },
  mounted() {
    this.init();
  },
};
</script>
<style lang="less" scoped>
.text-tips {
  background: #fff;
  overflow: hidden;
  position: relative;
  .title {
    background: red;
    right: 0;
    line-height: 30px;
    padding: 2px 13px;
    position: absolute;
    color: #fff;
    min-width: 100px;
    text-align: center;
    border-bottom-left-radius: 20px;
  }

  .text {
    height: calc(100% - 30px);
    margin-top: 30px;
    padding: 6px;
    overflow-y: auto;
  }
}
</style>