<template>
  <canvas
    class="canvas-container"
    ref="board"
    :width="width+'px'"
    :height="height+'px'"
    :style="{'pointer-events':enable?'auto':'none'}"
    @mousedown="beginPaintHandler"
    @mousemove="paintingHandler"
  ></canvas>
</template>

<script>
export default {
  name: 'Painter',
  components: {},
  props: {
    enable: {
      type: Boolean,
      default: true
    },
    emitEnable: {
      type: Boolean,
      default: false
    },
    receiveEnable: {
      type: Boolean,
      default: false
    },
    width: {
      type: Number,
      default: 800
    },
    height: {
      type: Number,
      default: 600
    },
    ratio: {
      type: Number,
      default: 1
    },
    lineWidth: {
      type: Number,
      default: 3
    },
    lineColor: {
      type: String,
      default: "#000000"
    },
  },
  data() {
    return {
      x: 0,
      y: 0,
      mouseDown: false,
      context: {},
      line: {}
    };
  },
  computed: {
  },
  methods: {
    beginPaintHandler($event = {}) {
      const { target, offsetX, offsetY, button } = $event;
      if (target.localName != "canvas" || button != 0) return;
      let
        x = offsetX / this.ratio,
        y = offsetY / this.ratio;
      x = Number(x.toFixed(1));
      y = Number(y.toFixed(1));
      this.mouseDown = true;
      this.beginPaint(x, y, this.lineWidth / this.ratio, this.lineColor);
      this.emitEnable && this.$emit('beginPaint', x, y);
    },
    beginPaint(x, y, width, color) {
      this.x = x;
      this.y = y;
      this.context.beginPath();
      this.context.lineJoin = "round";
      this.context.lineWidth = width;
      this.context.strokeStyle = color;
    },
    paintingHandler($event = {}) {
      if (!this.mouseDown) return;
      const { target, offsetX, offsetY } = $event;
      if (target.localName != "canvas") return;
      let
        x = offsetX / this.ratio,
        y = offsetY / this.ratio;
      x = Number(x.toFixed(1));
      y = Number(y.toFixed(1));
      this.painting(x, y);
      this.emitEnable && this.$emit('painting', x, y);
    },
    painting(x, y) {
      this.context.moveTo(this.x, this.y);
      this.context.lineTo(x, y);
      this.context.stroke();
      this.context.closePath();
      this.x = x;
      this.y = y;
    },
    endPaintHandler() {
      if (this.mouseDown) {
        this.mouseDown = false;
        this.$emit('endPaint');
        this.$painter.pushData(this.$refs['board'].toDataURL());
      }
    },
    snapshotChange(imageData) {
      this.clear();
       console.log('output ->', this.$painter.step);
       if(imageData){
         this.context.drawImage(imageData, 0, 0, this.width, this.height);
       }
    },
    clear() {
      this.context.clearRect(0, 0, this.width, this.height);
    },
    listen() {
      if (!this.$painter) return;
      this.$painter.on('clear', this.clear)
      this.$painter.on('snapshotChange', (data) => {
        this.snapshotChange(data);
      });

      if (!this.receiveEnable) return;
      this.$painter.on('beginPaint', this.beginPaint);
      this.$painter.on('painting', this.painting);

    }
  },
  created() {
    this.listen()
  },
  mounted() {
    try {
      this.context = this.$refs['board'].getContext("2d");
      document.addEventListener("mouseup", this.endPaintHandler, false);
    } catch (_) {
      return
    }
  },
};
</script>

<style lang="less" scoped>
.canvas-container {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 3;
}
</style>
