<template>
  <div class="base-alert">
    <p class="title" v-if="popdata.title">{{popdata.title}}</p>
    <div class="video-body">
      <div class="view">
        <video id="videoView" playsinline autoplay></video>
      </div>
      <div class="device-list">
        <span class="cam_title">
          {{$t('video_camera')}}
          <small v-if="camera_error">{{camera_error}}</small>
        </span>
        <dl @mouseleave="showCamera(false)">
          <dt :value="camera.deviceId" @click="showCamera(true)">
            <span>{{camera.label}}</span>
            <i></i>
          </dt>
          <dd v-show="camShow">
            <p
              v-for="device in cameras"
              @click="changeCamera(device)"
              :key="device.deviceId"
              :value="device.deviceId"
            >{{device.label}}</p>
          </dd>
        </dl>
        <span>{{$t('video_microphone')}}</span>
        <dl @mouseleave="showMic(false)">
          <dt :value="microphone.deviceId" @click="showMic(true)">
            <span>{{microphone.label}}</span>
            <i></i>
          </dt>
          <dd v-show="micShow">
            <p
              v-for="device in microphones"
              @click="changeMicrophone(device)"
              :key="device.deviceId"
              :value="device.deviceId"
            >{{device.label}}</p>
          </dd>
        </dl>
        <div class="audio-track">
          <span class="audio-level" :style="{width: level * 250 + 'px'}"></span>
        </div>
      </div>
    </div>

    <div class="btn-group">
      <button
        type="button"
        name="button"
        class="ko"
        v-if="popdata.cancellabel"
        @click="koClick"
      >{{popdata.cancellabel}}</button>
      <button
        type="button"
        name="button"
        class="ok"
        v-if="popdata.label"
        @click="okClick"
      >{{popdata.label}}</button>
    </div>
  </div>
</template>

<script>
import { VideoStream } from "@/plugins/WebVideoClient";
import Api from "@/service";
export default {
  name: "VideoSetter",
  props: ["popdata"],
  data() {
    return {
      level: 0,
      intervalId: 0,
      viewStream: null,
      camera_error: "",
      camShow: false,
      micShow: false
    };
  },
  computed: {
    cameras() {
      return (this.viewStream && this.viewStream.cameras) || [];
    },
    microphones() {
      return (this.viewStream && this.viewStream.microphones) || [];
    },
    camera() {
      return this.viewStream && this.viewStream.camera || {};
    },
    microphone() {
      return this.viewStream && this.viewStream.microphone || {};
    }
  },
  methods: {
    okClick() {
      const that = this;
      if (this.viewStream) {
        this.videoStream.cameraId = this.viewStream.cameraId;
        this.videoStream.microphoneId = this.viewStream.microphoneId;
        this.videoClient.stop(this.videoStream.localStream);
        this.videoStream.close();

        setTimeout(() => {
          that.videoStream.createStream().then(stream => {
            that.camera_error = "";
            that.videoStream.display();
            that.videoClient.publish(stream);

            that.Api.saveDevice(that.videoStream.camera, that.videoStream.microphone).finally(() => {
              that.close();
              that.$emit("submit");
              that.$sensor.emit('setting', { settingmicrophone: that.videoStream.microphone.label, settingcamerca: that.videoStream.camera.label, issettingsuccess: true })
            });
          }).catch(err => {
            that.koClick();
            that.camera_error = that.$t("video_change_error");
            that.alert.show({
              title: that.$t('video_change_error_title'),
              text: that.$t('video_change_error_text'),
              label: that.$t('alert_error_refresh_label')
            }, cb => {
              window.location.reload();
            });
          });
        }, 1000);
      }
    },
    koClick() {
      this.close();
      this.$emit("cancel");
    },
    showCamera(show) {
      this.camShow = show;
    },
    showMic(show) {
      this.micShow = show;
    },
    async saveUserDevice(cam, mic) {
      await this.Api.saveDevice(cam, mic);
    },
    init() {
      var that = this;
      that.viewStream = new VideoStream();
      that.viewStream.streamID = 6666;
      that.viewStream.videoElement = document.querySelector("#videoView");
      that.viewStream.videoPrifile = that.videoStream.videoPrifile;
      that.viewStream.init().then(list => {
        that.viewStream.cameraId = that.videoStream.cameraId;
        that.viewStream.microphoneId = that.videoStream.microphoneId;
        that.viewStream.createStream().then(stream => {
          that.camera_error = "";
          that.viewStream.display();
          that.intervalId = setInterval(() => {
            that.level = stream.getAudioLevel();
          }, 100);
        }).catch(err => {
          that.camera_error = that.$t("video_change_error");
        });
      });
    },
    changeCamera(item) {
      this.showCamera(false);
      const that = this;
      this.viewStream.close();
      this.viewStream.cameraId = item.deviceId;
      // this.viewStream.switchDevice('video', item.deviceId);
      this.viewStream.createStream().then(stream => {
        that.camera_error = "";
        that.viewStream.display();
      }).catch(err => {
        that.camera_error = that.$t("video_change_error");
      });
    },
    changeMicrophone(item) {
      this.showMic(false);
      const that = this;
      this.viewStream.close();
      this.viewStream.microphoneId = item.deviceId;
      this.viewStream.createStream().then(stream => {
        that.viewStream.display();
        clearInterval(that.intervalId);
        that.intervalId = setInterval(() => {
          that.level = stream.getAudioLevel();
        }, 100);
      });
    },
    close() {
      this.viewStream.close();
      this.viewStream = null;
    }
  },
  mounted() {
    this.init();
  }
};
</script>

