<template>
  <transition enter-active-class="animated heartBeat">
    <div class="timer">
      <IconFont
        class="icon-font"
        icon="uu-Clock"
      />
      {{time}}
    </div>
  </transition>
</template>

<script>
import IconFont from './IconFont.vue';
export default {
  name: 'Clock',
  components: {
    IconFont
  },
  data(){
    return{
      time:0
    }
  },
  created() {
    this.$clock.on('change', () => {
      this.time = this.$clock.formatTimeValue(this.$clock.startTimeDifference);
    });
  }
}
</script>

<style lang="less" scoped>
.timer {
  color: #ffff;
  user-select: none;
  font-size: 13px;
  display: flex;
  align-items: center;

  .icon-font {
    width: 13px;
    height: 13px;
    color: #6d93f2;
    margin-right: 10px;
  }
}
</style>