import BaseEvent from './BaseEvent'
class SplashEvent extends BaseEvent {
    constructor() {
        super();
    }

    init(total) {
        this.emit('init', total);
    }

    reset(text) {
        this.emit('reset', text);
    }

    progress(step, text) {
        this.emit('progress', step, text);
    }

    complete(text) {
        this.emit('complete', text);
    }
}

export default SplashEvent;