    <div v-if="popdata.icon" :class="['icon', popdata.icon]"></div>
    <p class="title" v-if="popdata.title">{{popdata.title}}</p>
    <p class="text" v-html="popdata.text"></p>
    <div class="btn-group">
      <button
        type="button"
        name="button"
        class="ko"
        v-if="popdata.cancellabel"
        @click="koClick"
      >{{popdata.cancellabel}}</button>
      <button
        type="button"
        name="button"
        class="ok"
        v-if="popdata.label"
        @click="okClick"
      >{{popdata.label}}</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "BaseWindow",
  props: {
    popdata: { type: Object, default: () => { } },
  },
  methods: {
    okClick() {
      this.$emit("submit");
    },
    koClick() {
      this.$emit("cancel");
    }
  },
  mounted() {
  }
};
</script>

