import TextBox from '@/components/TextBox.vue'
import event from 'events';

class TextBoxEvent extends event.EventEmitter {

    history = [];
    step = 0;
    constructor() {
        super();
    }

    /**
     * 向队列中添加数据
     * 如果是是撤销后的数据，此时需要push，则移除此step之后的所有数据
     * @param {object} data 
     */
    pushData(data) {
        // if(this.step > 0 && this.step != this.history.length){
        //     this.history = this.history.slice(this.step);
        // }
        this.step++;
        if (data) {
            data.isInput = false;
        }
        this.history.push(data);
    }

    undo() {
        if (this.step > 0) {
            this.step--;
            let data = this.history.slice(0, this.step);
            this.emit('snapshotChange', data, this.step, this.history.length);
        }
    }

    redo() {
        if (this.step < this.history.length) {
            this.step++;
            let data = this.history.slice(0, this.step);
            this.emit('snapshotChange', data, this.step, this.history.length);
        }
    }

    beginWrite(x, y, width, height) {
        this.emit('beginWrite', x, y, width, height);
    }

    writing(x, y, width, height, fontSize, color, text, index) {
        this.emit('writing', x, y, width, height, fontSize, color, text, index)
    }

    clear() {
        this.emit('clear')
    }

}

export {
    TextBox,
    TextBoxEvent
}